import React, { useState, useRef, useEffect } from "react";
import { Box, Flex, Container } from "@rent_avail/layout";
import {
  motion,
  AnimateSharedLayout,
  useViewportScroll,
  useTransform,
  useMotionValue
} from "framer-motion";
import { Menu, Bell } from "react-feather";

const tabs = ["Lease & Rent", "Leads & Applications", "Listing", "Maintenance"];

export function Header() {
  const headerRef = useRef(null);
  const [selectedTab, setTab] = useState(tabs[0]);
  const { scrollY } = useViewportScroll();
  const scroll = useMotionValue(0);
  const opacity = useTransform(scroll, [0, 48], [1, 0]);
  const height = useTransform(scroll, [72, 172], [172, 72]);
  const y = useTransform(scroll, [72, 172], [0, -46]);
  const x = useTransform(scroll, [72, 172], [0, 48]);
  const scale = useTransform(scroll, [72, 172], [1, 0.75]);
  useEffect(() => {
    function handleUpdate() {
      const velocity = scrollY.getVelocity();
      const direction = Math.sign(velocity);
      const newScroll =
        direction > 0
          ? scroll.get() + (scrollY.get() - scrollY.getPrevious())
          : scroll.get() - (scrollY.getPrevious() - scrollY.get());
      const change = Math.max(Math.min(newScroll, 172), 0);
      scroll.set(change);
    }
    const clear = scrollY.onChange(handleUpdate);
    return () => clear();
  });
  return (
    <Box
      ref={headerRef}
      as={motion.header}
      style={{ height }}
      sx={{
        bg: "blue_500",
        color: "ui_100",
        position: "fixed",
        top: 0,
        left: 0,
        width: "100%",
        overflow: "hidden"
      }}
    >
      <Container px="0">
        <Flex as="nav" alignItems="center" p="2rem">
          <Box as={Menu} mr="auto" />
          <Box as={Bell} mr="2rem" />
          <Box width="2rem" height="2rem" bg="ui_300" borderRadius="50%" />
        </Flex>
        <Box
          layout
          as={motion.h4}
          style={{ x, y, scale, transformOrigin: "0 0 0" }}
          sx={{
            flex: 1,
            px: "2rem",
            whiteSpace: "nowrap",
            overflow: "hidden",
            textOverflow: "ellipsis",
            transformOrigin: "0 0 !important"
          }}
        >
          1234 N Martin Luther King Jr. Blvd.
        </Box>
        <Box as="nav" mt="2rem">
          <AnimateSharedLayout>
            <Flex
              as={motion.ul}
              style={{ opacity }}
              sx={{
                listStyle: "none",
                overflowX: "auto",
                px: "2rem",
                "li:not(:last-of-type)": { mr: "2rem" }
              }}
            >
              {tabs.map((tab) => (
                <Tab
                  key={tab}
                  tab={tab}
                  isSelected={tab === selectedTab}
                  onClick={(e) => setTab(tab)}
                />
              ))}
            </Flex>
          </AnimateSharedLayout>
        </Box>
      </Container>
    </Box>
  );
}

function Tab({ isSelected, tab, ...props }) {
  return (
    <Box
      {...props}
      layout
      as={motion.li}
      sx={{
        position: "relative",
        color: isSelected ? "ui_100" : "blue_300",
        flex: "0 0 auto",
        whiteSpace: "nowrap",
        cursor: "pointer",
        transition: "200ms",
        pb: "2rem",
        "&:hover": {
          color: "ui_100"
        }
      }}
    >
      <span>{tab}</span>
      {isSelected && (
        <Box
          as={motion.div}
          layoutId="underline"
          sx={{
            position: "absolute",
            bottom: "0",
            left: "-0.5rem",
            width: "calc(100% + 1rem)",
            height: "4px",
            bg: "green_500"
          }}
        />
      )}
    </Box>
  );
}
